.nav-drawer-position {
    top: 0;
    left: 0;
    background: black;
    position: absolute;
}

.nav-drawer-btn {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.nav-drawer-btn:focus {
    outline: none;
}


.nav-drawer-btn__line {
    width: 30px;
    height: 2px;
    background: rgb(0, 24, 236);
}
.navbar {
    background-image: -webkit-linear-gradient(top,
    rgb(16, 63, 78), rgb(2, 24, 31));
    background-image: linear-gradient(-900deg,
    rgb(16, 63, 78), rgb(2, 24, 31));
    height: 56px;
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    z-index: 1000; 
}

.nav-drawer {
    top:10;
    left:0;
    background: rgb(36, 44, 0);
    position: absolute;
    z-index: 2100;
}

.navbar-logo {
    background: url(data:image/png;base64,) -80px -80px;
    font-family: fantasy;
    font-size: 30px;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    letter-spacing: 1px;
    z-index: 400;
    left: 42px;
    position: absolute;
}

.navbar-logo a{
    text-decoration: none;
}

.navbar * {
    display: inline;
}

@media (max-width: 500px) {
    .navbar-menu-items .open {
        text-decoration: none;
        position: flex;
        top: 65px;
        left: 0;
    }
    .navbar-menu-items {
        text-decoration: none;
        position: flex;
        top: 65px;
        left: 0;
    }
}

.navbar-menu-items {
    right: 20px;
    background: none;
    position: absolute;
}

.navbar-menu-items a:hover {
    background-color: #0f0f0f;
    color: white;
}

.navbar-menu-items a {    
    background: rgb(177, 170, 170);
    color: rgb(0, 0, 0);
    padding: 10px 10px;
    text-decoration: none;
    font-family: 'Times New Roman', Times, serif;
}



